import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { Helps } from './Help';
import { HelpServicesService } from './help-services.service';

@Component({
  selector: 'app-help',
  templateUrl: './help.component.html',
  styleUrls: ['./help.component.css']
})
export class HelpComponent implements OnInit {

  public loading = true;
  public errorMesage: string;
  public successMessage: string;
  public name: string;
  public title: string;
  public nameHelp: string;

  constructor(public helpService: HelpServicesService) { }

  ngOnInit() {

  }

  onSubmit() {
    alert("Your criteria has been Reocored, Will get back to you soon");
  }

  createHelp() {
    this.successMessage = '';
    this.errorMesage = '';
    this.helpService.createHelp(this.name, this.title, this.nameHelp)
    .subscribe((Helped: Helps)=>{
      this.name = '';
      this.title = '';
      this.nameHelp = '';

    });
  }

}

