import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Helps } from './Help';
@Injectable({
  providedIn: 'root'
})
export class HelpServicesService {

  private BASE_URL = environment.API_URL;

  constructor(private http: HttpClient) {
    // this.http.get(`this.${this.BASE_URL}/help`);
   }

  getHelps(): Observable<Helps[]> {
    return this.http.get<Helps[]>(`${this.BASE_URL}/help`);
  }

  createHelp(name: string, title: string, nameHelp: string): Observable<Helps> {
    return this.http.post<Helps>(`${this.BASE_URL}/help`, {name, title, nameHelp});
  }
}
